import './Waves/Style.scss';

export default function Waves() {
  return (
    <div className="flex-column waves">
      <svg
        viewBox="0 24 150 28"
        preserveAspectRatio="none"
        shapeRendering="auto">
        <defs>
          <path
            id="gentle-wave"
            d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z"
          />
        </defs>
        <g>
          <use
            xlinkHref="#gentle-wave"
            x="48"
            y="0"
            fill="rgba(56, 144, 200, 0.7"
          />
          <use
            xlinkHref="#gentle-wave"
            x="48"
            y="3"
            fill="rgba(56, 144, 200, 0.5)"
          />
          <use
            xlinkHref="#gentle-wave"
            x="48"
            y="5"
            fill="rgba(56, 144, 200, 0.3)"
          />
          <use xlinkHref="#gentle-wave" x="48" y="7" fill="rgba(56, 144, 200, 0.9)" />
        </g>
      </svg>
      <div/>
    </div>
  );
};