export default function Home() {
  return (
    <div
    className="center-content"
    style={{
      marginTop: "2rem",
      maxWidth: "75%"
    }}>
      <img
      alt="portrait"
      className="circle responsive-img"
      src="/img/portrait.png"
      style={{
        maxHeight: 360,
        maxWidth: 360,
        width: "100%"
      }} />
    </div>
  );
};